import React from "react";
import NavigationBar from "../../Components/LeadDashboardExtended/NavigationBar/NavigationBar";
import LeadDashboardSidebar from "../../Components/LeadDashboard/LeadDashboardSidebar/LeadDashboardSidebar";
import classes from "./LeadDashboardExtended.module.css";
import { useBusiness } from "../../contexts/BusinessProvider";
import BottomNavigation from "../../shared-components/mobile/BottomNavigation/BottomNavigation";
// component imports
import LeadDataTable from "../../Components/LeadDashboardExtended/LeadDataTable";
import "antd/dist/antd.css";

import { Grid, Hidden } from "@material-ui/core";

const LeadDashboardExtended = ({ history, location }) => {
  const { allBusiness, selectedBusiness, switchBusiness, isLoading } =
    useBusiness();
  return (
    <>
      <div className={classes.Container}>
        <div className={classes.inner}>
          <LeadDashboardSidebar
            allBusiness={allBusiness}
            selectedBusiness={selectedBusiness}
            className={classes.Sidebar}
            location={location}
            isLoading={isLoading}
            switchBusiness={switchBusiness}
            history={history}
          />
          <div className={classes.Content}>
            <NavigationBar />

            <Grid lg={11} md={11} sm={11} xs={11} className={classes.Grid}>
              <LeadDataTable />
            </Grid>
          </div>
        </div>
      </div>
      <Hidden implementation="js" smUp>
        <BottomNavigation />
      </Hidden>
    </>
  );
};

export default LeadDashboardExtended;
