import React, { useEffect, useRef, useState } from "react";

// styles
import "./styles/index.css";

// context imports
import LeadProviderProvider from "../../../contexts/LeadDashboardProvider";
import { useToastMessage } from "../../../contexts";
import { fetchRegionData } from "../../../api/LeadDashboard";
import { Button } from "antd";

const LeadDataTable = () => {
  // context data
  const { setErrorMessage } = useToastMessage();

  const [data, setData] = useState([]);

  const [totals, setTotals] = useState({
    total1: 0,
    total2: 0,
    total3: 0,
    onlyNumber: 0,
  });

  const fetchDataRef = useRef(async () => {
    try {
      const res = await fetchRegionData();

      if (res.error) {
        return setErrorMessage(res.error);
      }

      setTotals({
        total1: res.total1,
        total2: res.total2,
        total3: res.total3,
        onlyNumber: res.onlyNumber,
      });
      return setData(res.data);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  });

  useEffect(() => {
    fetchDataRef.current();
  }, []);

  return (
    <div className="container">
      {/* <p className="display-header">
        Chatbot Enq data till{" "}
        {new Date().toLocaleString("en-US", {
          hour: "numeric",
          minute: "numeric",
          hour12: true,
        })}{" "}
        (
        {new Date().toLocaleString("en-US", {
          day: "numeric",
          month: "short",
        })}
        )
      </p> */}
      <Button onClick={() => window.open("/engage/lead-dashboard", "_self")}>
        Go Back
      </Button>
      <table>
        <tr className="head-bg">
          <th>Region</th>
          <th>Enquires With Dealership Selection</th>
          <th>Enquires Without Dealership Selection</th>
          <th>Total Enquires</th>
          <th>Enquires with only Contact Number</th>
          <th rowSpan={7} className="together">
            All enquires together{" "}
            {`(${totals.total1} + ${totals.total2} + ${totals.onlyNumber}) = ${
              totals.total1 + totals.total2 + totals.onlyNumber
            }`}
          </th>
        </tr>

        {data.map((el, i) => {
          return (
            <tr key={i}>
              <td>{el.region}</td>
              <td>{el.assignedLeads}</td>
              <td>{el.departmentLeads}</td>
              <td>{el.total}</td>

              {i === 0 && (
                <td key={i} rowSpan={5}>
                  {totals.onlyNumbeleadr}
                </td>
              )}
            </tr>
          );
        })}

        <tr className="total">
          <td>Total</td>
          <td>{totals.total1}</td>
          <td>{totals.total2}</td>
          <td>{totals.total3}</td>
          <td>{totals.onlyNumber}</td>
        </tr>
      </table>
    </div>
  );
};

// eslint-disable-next-line import/no-anonymous-default-export
export default (props) => (
  <LeadProviderProvider>
    <LeadDataTable {...props} />
  </LeadProviderProvider>
);
