import moment from "moment";
import React, { useContext, useEffect, useState } from "react";
import { Role, tableViews } from "../Constants/LeadDahboard";
import { fetchIsDepartmentHead } from "../api/Auth";
import {
  fetchDownloadLeadReport,
  FetchMonthlyLeadsData,
  fetchTableDataGet,
  FetchUAWOLLeadsData,
  fetchUnassignedLeads,
  fetchUnassignedLeadsCount,
} from "../api/LeadDashboard";
import { useToastMessage } from "./ToastMessageContext";
import { capitalizeFirstLetter, JSONToCSVConvertor } from "../Shared/Utility";

export const LeadDashboardContext = React.createContext();

export const useLeadDashboard = () => useContext(LeadDashboardContext);

const LeadDashboardProvider = ({ children }) => {
  const [isFilterDialogOpen, openFilterDialog] = useState(false);

  const [pageView, setPageView] = useState("dashboard");

  const [isLoading, setLoading] = useState(false);
  const { setErrorMessage } = useToastMessage();

  const [currentTableView, setCurrentTableView] = useState(null);
  const [currentTableId, setCurrentTableId] = useState(null);
  const availableBikes = [
    "Activa 6G",
    "Shine",
    "Dio",
    "Grazia",
    "Activa 125",
    "CD110",
    "Livo",
    "SP 125",
    "Unicorn",
    "X-Blade",
    "Hornet 2.0",
    "CB200X",
  ];

  const [breadcrumbView, setBreadcrumbView] = useState([]);
  const [availableViews, setAvailableViews] = useState({});
  const [filterValue, setFilterValue] = useState({
    vehicle: [],
  });
  const [unassigned, setUnassigned] = useState(false);

  const [filterDate, setFilterDate] = useState({
    from: moment().subtract("1", "months").hour(0).minute(0).second(0).toDate(),
    to: moment().hour(24).minute(0).second(0).toDate(),
  });

  const [isOpenAssignPage, setOpenAssignPage] = useState(false);

  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: 10,
    total: 0,
  });

  const [userRole, setUserRole] = useState(null);

  const [data, setData] = useState([]);
  const [originTableData, setOriginTableData] = useState([]);
  const [reportData, setReportData] = useState([]);

  useEffect(() => {
    setPagination({ current: 1, pageSize: 10, total: 0 });
    setData([]);
    setUnassigned(false);
    setFilterValue({
      vehicle: [],
    });
    setBreadcrumbView([]);
    setCurrentTableId(null);
    setCurrentTableView(null);
    setReportData([]);
  }, [pageView]);

  const [totalUnassignedLeads, setTotalUnassignedLeads] = useState(0);

  const getTableData = async ({ query, data = { filter: {} } }) => {
    try {
      const res = await fetchTableDataGet({ query, data });

      if (!res.success) return setErrorMessage(res.message);
      if (!res.payload) return;

      setPagination((prevState) => ({
        ...prevState,
        total: res.payload.meta?.total || res.payload.data.length,
        pageSize: 10,
        current: +res.payload.meta?.current_page || 1,
      }));

      setReportData([]);

      return setData(res.payload.data);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const getLeadReportForDownload = async ({ sheetName }) => {
    try {
      setReportData([]);
      const res = await fetchDownloadLeadReport({
        query: `?r=${userRole}&view=${currentTableView}${
          (availableViews && `&id=${currentTableId || availableViews.id}`) || ""
        }`,
        data: {
          filter: filterValue || {},
          date: filterDate,
          unassigned: unassigned,
        },
      });

      if (!res.success) return setErrorMessage(res.message);
      JSONToCSVConvertor(
        res.payload,
        capitalizeFirstLetter(sheetName || "Sheet"),
        true
      );

      return setReportData(res.payload);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const getUnassignedLeadsForDownload = async ({ sheetName }) => {
    try {
      FetchUAWOLLeadsData(filterDate)
        .then((data) => {
          // convert the data to excel format and set the state
          JSONToCSVConvertor(
            data.payload,
            capitalizeFirstLetter(sheetName || "Sheet"),
            true
          );
        })
        .catch((error) => {
          return setErrorMessage(error);
        });
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const getMonthlyLeadsForDownload = async ({ sheetName, selMonth }) => {
    try {
      const sheet_name = `${sheetName}-${selMonth}`;

      FetchMonthlyLeadsData(filterDate)
        .then((data) => {
          // convert the data to excel format and set the state
          JSONToCSVConvertor(
            data.data,
            capitalizeFirstLetter(sheet_name),
            true
          );
        })
        .catch((error) => {
          return setErrorMessage(error.message);
        });
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const getUnassignedLeadsCount = async (date = undefined) => {
    try {
      const res = await fetchUnassignedLeadsCount({
        data: { date: date || filterDate },
      });
      if (!res.success) return setErrorMessage(res.message);
      const dataSize = Number(res.payload.total || 0);
      setTotalUnassignedLeads(dataSize);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const checkUserStatus = async () => {
    try {
      setLoading(true);

      const departmentId = localStorage.getItem("departmentId") || null;
      const teamId = localStorage.getItem("teamId") || null;

      const res = await fetchIsDepartmentHead(departmentId, teamId);

      if (!res.success) {
        setLoading(false);
        return setErrorMessage(res.message);
      }

      const tableView = tableViews.find((e) => e.role === res.payload.role_id);

      // SET Current user Role
      setUserRole(tableView.role);

      if ([Role.ADMIN, Role.OWNER].includes(tableView.role))
        getUnassignedLeadsCount();

      await getTableData({
        query: `?r=${tableView.role}&view=${tableView.views[0]}${
          localStorage.getItem(tableView.id)
            ? `&id=${localStorage.getItem(tableView.id)}`
            : ""
        }`,
        data: {
          date: filterDate,
          filter: filterValue || {},
          unassigned: unassigned,
        },
      });

      setAvailableViews({
        ...tableView,
        id: localStorage.getItem(tableView.id) || tableView.id,
      });

      setCurrentTableView(
        tableView ? (tableView.views ? tableView.views[0] : null) : null
      );

      setBreadcrumbView((prevState) => {
        prevState.push({
          id: localStorage.getItem(tableView.id) || tableView.role,
          parent: true,
          role: tableView.views[0],
        });

        return prevState;
      });

      setLoading(false);
    } catch (err) {
      return setErrorMessage(err.message);
    }
  };

  const getUnassignedLeads = async ({ pagination, date }) => {
    try {
      setLoading(true);

      const res = await fetchUnassignedLeads({
        query: `?limit=${pagination.pageSize}&page=${pagination.current}`,
        data: { date: date || filterDate },
      });

      if (!res.success) {
        setLoading(false);
        return setErrorMessage(res.message);
      }

      setPagination((prevState) => ({
        ...prevState,
        total: res.payload.meta.total,
        pageSize: res.payload.meta.per_page,
      }));

      setLoading(false);

      return setData(res.payload.data);
    } catch (err) {
      setLoading(false);
      return setErrorMessage(err.message);
    }
  };

  const getTableDataBasedOnView = async ({ id, tableView }) => {
    try {
      setLoading(true);

      await getTableData({
        query: `?r=${userRole}&view=${tableView}${id && `&id=${id}`}`,
        data: {
          filter: filterValue || {},
          date: filterDate,
          unassigned: unassigned,
        },
      });

      setCurrentTableView(tableView);
      setCurrentTableId(id);

      setLoading(false);
    } catch (err) {
      setLoading(false);
      setErrorMessage(err.message);
    }
  };

  const applyFilter = async (value) => {
    try {
      setLoading(true);

      await getTableData({
        query: `?r=${userRole}&view=${currentTableView}${
          (availableViews && `&id=${currentTableId || availableViews.id}`) || ""
        }`,
        data: {
          filter: value || {},
          date: filterDate,
          unassigned: unassigned,
        },
      });

      setReportData([]);

      setLoading(false);
    } catch (err) {
      setErrorMessage(err.message);
    }
  };

  const applyDateFilter = async (date, key) => {
    setFilterDate((prevState) => ({
      ...prevState,
      [key]: date,
    }));

    setReportData([]);

    if (pageView !== "dashboard") {
      return await getUnassignedLeads({
        pagination,
        date: { ...filterDate, [key]: date },
      });
    } else {
      try {
        if (isLoading) return;

        setLoading(true);

        if ([Role.ADMIN, Role.OWNER].includes(userRole))
          getUnassignedLeadsCount({ ...filterDate, [key]: date });

        await getTableData({
          query: `?r=${userRole}&view=${currentTableView}${
            (availableViews && `&id=${currentTableId || availableViews.id}`) ||
            ""
          }`,
          data: {
            filter: filterValue,
            date: { ...filterDate, [key]: date },
            unassigned: unassigned,
          },
        });

        setLoading(false);
      } catch (err) {
        setErrorMessage(err.message);
      }
    }
  };

  const applyAssignedFilter = async (value) => {
    try {
      if (isLoading) return;

      setLoading(true);
      setUnassigned(value);

      //  if true, clear all bike filters
      if (value) {
        setFilterValue((prevState) => ({
          ...prevState,
          vehicle: [],
        }));
      } else {
        setFilterValue((prevState) => ({
          ...prevState,
          vehicle: availableBikes,
        }));
      }

      await getTableData({
        query: `?r=${userRole}&view=${currentTableView}${
          (availableViews && `&id=${availableViews.id || currentTableId}`) || ""
        }`,
        data: {
          filter: {},
          date: filterDate,
          unassigned: value,
        },
      });

      setLoading(false);
    } catch (err) {
      setErrorMessage(err.message);
    }
  };

  const refreshTable = async () => {
    try {
      if (isLoading) return;

      setLoading(true);

      await getTableData({
        query: `?r=${userRole}&view=${currentTableView}${
          (availableViews && `&id=${availableViews.id || currentTableId}`) || ""
        }`,
        data: {
          filter: filterValue || {},
          date: filterDate,
          unassigned: unassigned,
        },
      });

      setLoading(false);
    } catch (err) {
      setErrorMessage(err.message);
    }
  };

  const getNextPageData = async (pageData, filters, sorter, extra) => {
    try {
      if (
        isLoading &&
        (currentTableView !== "dealership" || currentTableView !== "lead")
      )
        return;

      if (pageData.current === pagination.current) {
        return;
      }

      setLoading(true);

      await getTableData({
        query: `?r=${userRole}&view=${currentTableView}${
          (availableViews && `&id=${currentTableId || availableViews.id}`) || ""
        }&page=${pageData.current}&size=${pageData.pageSize}`,
        data: {
          filter: filterValue || {},
          date: filterDate,
          unassigned: unassigned,
        },
      });

      setLoading(false);
    } catch (err) {
      setErrorMessage(err.message);
    }
  };

  const value = {
    isLoading,
    currentTableView,
    setCurrentTableView,
    checkUserStatus,
    data,
    pagination,
    availableViews,
    getTableDataBasedOnView,
    breadcrumbView,
    setBreadcrumbView,
    filterValue,
    setFilterValue,
    applyFilter,
    filterDate,
    setFilterDate,
    unassigned,
    applyAssignedFilter,
    applyDateFilter,
    userRole,
    setUnassigned,
    isOpenAssignPage,
    setOpenAssignPage,
    refreshTable,
    isFilterDialogOpen,
    openFilterDialog,
    pageView,
    setPageView,
    getUnassignedLeads,
    setPagination,
    getUnassignedLeadsCount,
    totalUnassignedLeads,
    originTableData,
    setOriginTableData,
    getLeadReportForDownload,
    reportData,
    currentTableId,
    getNextPageData,
    getUnassignedLeadsForDownload,
  };

  return (
    <LeadDashboardContext.Provider value={value}>
      {children}
    </LeadDashboardContext.Provider>
  );
};

export default LeadDashboardProvider;
