import { Hidden } from "@material-ui/core";
import { useState } from "react";
import SidebarDesktop from "../../Sidebar/Sidebar";
import { NavbarContainer } from "../../../shared-components/mobile";

const LeadDashboardSidebar = ({
  allBusiness,
  location,
  selectedBusiness,
  className,
  switchBusiness,
  isLoading,
  history,
}) => {
  const [state, setState] = useState({
    toggled: location.hash === "#open" ? true : false,
  });

  const toggleBar = () => {
    if (state.toggled) {
      history.push("#close");
    }
    if (!state.toggled) {
      history.push("#open");
    }
    return setState({ ...state, toggled: !state.toggled });
  };

  return (
    <>
      {/* Mobile Only */}
      <Hidden smUp implementation="js">
        <NavbarContainer
          isLoading={isLoading}
          switchBusiness={switchBusiness}
          open={state.toggled}
          toggleBar={toggleBar}
        />
      </Hidden>

      {/* Desktop Only */}
      <Hidden xsDown implementation="js">
        <div style={{ display: "flex", alignItems: "center" }}>
          <SidebarDesktop
            className={className}
            selectedBusiness={selectedBusiness}
            business={allBusiness}
            open={state.toggled}
            location={location}
            switchBetweenBusiness={switchBusiness}
            handleClose={toggleBar}
          />
        </div>
      </Hidden>
    </>
  );
};

export default LeadDashboardSidebar;
